<template>
  <div class="comp-content">
    <!-- ,,: -->
    <router-link class="competitive" :to="{ path: '/competitive', query: { tag: curTag } }">
      <img class="competitive-img" v-lazy="firstCompetitivePlayList.coverImgUrl" alt="">
      <div class="competitive-descCon">
        <div class="competitive-descCon-title"></div>
        <!--  -->
        <div class="competitive-descCon-desc">{{firstCompetitivePlayList.name}}</div>
        <!--  -->
         <div class="competitive-descCon-desc2">{{firstCompetitivePlayList.copywriter}}</div>
      </div>
    </router-link>
    <!--  -->
    <div class="playlists-tags">
      <span v-for="(item, i) of tags" :key="i" :class="{'playlists-tags-span-active':item.name==curTag}" @click="changeTag(item.name)">{{
        item.name
      }}</span>
    </div>
    <div class="playlists">
      <playlists-component
        :playLists="playlists"
        :playListShow="playListShow"
        idKey="id"
        playCountKey="playCount"
        imgKey="coverImgUrl"
        nameKey="name"
        scrollLoad="true"
      ></playlists-component>
    </div>
  </div>
</template>

<script>
import playlistsComponent from "../components/playilistsComponent";
export default {
  data() {
    return {
      tags: [], //
      curTag: "",
      playListShow: false,
      playlists: [], //
      firstCompetitivePlayList:{},//
      scrollDom:"",//,
    };
  },
  mounted() {
    this.getTags();
    this.getFirstCompetitivePlayList();
  },
  methods: {
    getTags() {
      //()
      this.g
        .axios({
          url: "/playlist/hot",
        })
        .then((res) => {
          this.g.print("", res);
          this.tags = res.data.tags;
          this.curTag=this.tags[0].name;//
        })
        .catch((err) => {
          console.log(err);
        });
    },
    changeTag(name) {
      //,:
      this.curTag = name;
    },
    //
    getPlayLists() {
      this.playListShow = false;
      this.g
        .axios({
          url: "/top/playlist",
          params: {
            cat: this.curTag,
            order: "hot", //,new
          },
        })
        .then((res) => {
          this.g.print("", res);
          this.playlists = res.data.playlists;
          this.playListShow = true;

        })
        .catch((err) => {
          console.log(err);
        });
    },
    // 
    getFirstCompetitivePlayList() {
      this.g
        .axios({
          url: "/top/playlist/highquality",
          params: {
            cat: this.curTag,
            limit:1
          },
        })
        .then((res) => {
          this.g.print("", res);
         this.firstCompetitivePlayList=res.data.playlists[0]
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
  watch: {
    curTag() {
      this.getPlayLists();
      this.getFirstCompetitivePlayList()
    },
  },
  components: {
    playlistsComponent,
  },
};
</script>

<style scoped>
.competitive {
  display: block;
  width: 100%;
  height: 15rem;
  padding: 1rem;
  border-radius: 1rem;
}
.competitive-img{
  width: 13rem;
  height: 100%;
}
.competitive-img,.competitive-descCon{
display: inline-block;
vertical-align: middle;
}
.competitive-descCon{
  width: calc(100% - 13rem);
  padding-left: 1rem;
}
.competitive-descCon-title{
  display: inline-block;
  padding: 0.5rem 1rem;
  border: 1px solid gold;
  border-radius: 5rem;
}
.competitive-descCon-desc{
  font-size: 1.5rem;
  padding: 1rem 0 0.5rem;
}
.playlists-tags {
  padding: 0 1rem;
  text-align: right;
}
.playlists-tags span{
    margin: 0 0.5rem;
    cursor: pointer;
    padding: 0.3rem 1.2rem;
    border-radius: 1rem;
}
.playlists-tags-span-active{
  color: white;
  background-color: #409EFF;
}
</style>